import React, { Suspense } from 'react'
import {Routes,Route} from "react-router-dom";
const GetList = React.lazy(() => import('../data-harga-perikanan/list-harga-perikanan'))
const InsertPerikanan = React.lazy(() => import('../data-harga-perikanan/create-data-perikanan'))
const Dashboard = React.lazy(() => import('../dashboard/web-dashboard'))


export default function Main() {

    return(
        <Suspense fallback={<h3>loading...</h3>}>
            <Dashboard/>
            <div className='main'>
                    <Routes>
                    <Route path="/perikanan" element={<GetList />} />
                    <Route path="/insert-perikanan" element={<InsertPerikanan/>} />
                    </Routes>
            </div>
        </Suspense>
    )
}

